:root {
  font-family: Roboto, sans-serif;
}

body {
  letter-spacing: .1px;
  font-family: Roboto, sans-serif;
  line-height: 1.15;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
}

html {
  scroll-behavior: smooth;
}

h1, h2, h3, h4, h5, h6, ul, ol, p {
  margin: 0;
  padding: 0;
}

a, a:visited {
  color: currentColor;
  text-decoration: none;
}

figcaption {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  display: block;
}

.container {
  align-items: center;
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

@media tablet and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 75px;
  }
}

@media desktop and (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding: 0 70px;
  }
}

.header {
  background: url("background.0b1f29c7.jpg") center / cover no-repeat;
  height: 230px;
}

.movie-card {
  flex-direction: column;
  gap: 10px;
  width: 100%;
  max-width: 280px;
  display: flex;
}

.movie-card__image {
  border-radius: 5px;
  width: 100%;
}

.movie-card__text {
  text-align: left;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
}

.movie-card__text--title {
  color: #000;
  text-transform: uppercase;
  font-size: 12px;
}

.movie-card__text--info {
  color: #ff6b08;
  line-height: 14px;
}

@media (min-width: 768px) {
  .movie-card {
    gap: 8px;
    max-width: 336px;
  }
}

@media (min-width: 1024px) {
  .movie-card {
    gap: 12px;
    max-width: 395px;
  }

  .movie-card__text {
    font-size: 20px;
    line-height: 2;
  }

  .movie-card__text--title {
    font-size: 20px;
  }
}

.footer {
  color: #545454;
  background-color: #f7f7f7;
}

